.container-responsive {
  box-sizing: border-box;
  width: auto;
  padding-right: 20px;
  padding-left: 20px;

  @media @tablet {
    padding-right: 30px;
    padding-left: 30px;
  }

  @media @desktop {
    width: 1300px;
    margin: 0 auto;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.container-adaptive {
  box-sizing: border-box;
  width: 320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @media @tablet {
    width: 768px;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media @desktop {
    width: 1300px;
    padding-right: 40px;
    padding-left: 40px;
  }
}
